import getOssUploadFile from './ossUpload'
/**
 * @desc 
 * @param func 
 * @param wait 
 * @param immediate true false 
 */
export  function debounce (func, wait, immediate = false) {
    let timeout;
    return function () {
      let context = this;
      let args = arguments;
  
      if (timeout) clearTimeout(timeout);
      if (immediate) {
        var callNow = !timeout;
        timeout = setTimeout(() => {
          timeout = null;
        }, wait)
        if (callNow) func.apply(context, args)
      } else {
        timeout = setTimeout(function () {
          func.apply(context, args)
        }, wait);
      }
    }
}
/**
 * @description 
 * @param { array | number } value
 * @param { array } permissions   
 */
export const HasPermissionsAccess = (value = [],permissions = [])=>{
    let hasAccess = true
    //   
    if (Array.isArray(value)) {
      hasAccess = value.find((id)=> permissions.includes(id) ) ? true : false;
    } else {
      // 
      hasAccess = permissions.includes(value)
    }
    return hasAccess
}
